import { React, Component, render } from "./simple-react";

class MyComponent extends Component {
  render() {
    return (
      <div>
        <h1>my component</h1>
        {this.children}
      </div>
    );
  }
}

render(
  <div id="a" class="c">
    <div>abc</div>
    <div>def</div>
    <div>kkk</div>
  </div>,
  document.body
);
